@media (min-width: 768px) { .vexel-protect__visual { margin-top: 5px; width: 27px; flex-shrink: 0; margin-right: 18px; } }

.vexel-protect__visual .vexel-status { width: 27px; height: 32px; }

@media (min-width: 768px) { .vexel-protect__main { flex-grow: 1; } }

.vexel-protect__btns { display: flex; justify-content: flex-start; align-items: center; margin-left: -10px; margin-right: -10px; }

.vexel-protect__btn { padding-left: 10px; padding-right: 10px; flex-shrink: 0; }

@media (min-width: 768px) { .vexel-protect__btn { flex-grow: 1; } }

.vexel-protect__btn .btn, .vexel-protect__btn .ilink { width: 100%; }

.vexel-protect__info { color: #8D8D8D; margin: 15px 0; }

.vexel-protect__input { height: 40px; padding: 5px 15px; border: 0 none; background-color: #F1EDEA; }

.vexel-protect__input::-webkit-input-placeholder { color: rgba(26, 32, 45, 0.25); }

.vexel-protect__input::-moz-placeholder { color: rgba(26, 32, 45, 0.25); }

.vexel-protect__input::-ms-input-placeholder { color: rgba(26, 32, 45, 0.25); }

.vexel-on-way span { display: inline-block; vertical-align: baseline; }

.vexel-on-way__value { font-weight: 500; font-size: 18px; }

.vexel-on-way__label { font-size: 16px; color: rgba(26, 32, 45, 0.5); margin-left: 5px; }

.vexel-control__item:not(:last-child) { margin-bottom: 20px; }
